import Vue from 'vue'
export default {
	data() {
		return {
			orderInfo: null
		};
	},
	onShow() {
		this.GetUserInfo()
		
		console.log(this.orderInfo);
	},
	methods:{
		GetUserInfo(){
			var that = this
			uni.showLoading({
				title: '请稍等'
			})
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'user/info',
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						console.log(res);
						Vue.prototype.userServiceInfo = res.data.data
						that.orderInfo = res.data.data.order
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		switchStaff(){
			var that = this
			uni.navigateTo({
				url: '../switch-staff/switch-staff?orderId=' + that.orderInfo.id
			})
		},
		cancelService(){
			var that = this
			uni.showModal({
				content: '确定取消吗？',
				success(res) {
					if(res.confirm){
						uni.request({
								header: {
									token: that.Token
								},
								url: that.Server + 'order/cancel',
								data: {orderId: that.userServiceInfo.order.id},
								success(res) {
									if(res.data.code == 0){
										that.GetUserInfo()
										uni.showToast({
											icon: "none",
											title: '预约已取消'
										})
										setTimeout(() => {
											uni.navigateBack({})
										}, 1500)
										
									} else if (res.data.code == 101) {
										uni.showToast({
											icon: "none",
											title: '登录超时，将重新授权登录',
											duration: 1000
										})
										setTimeout(() => {
											uni.reLaunch({
												url: '../login/login'
											})
										}, 2000)
									} else {
										uni.showToast({
											icon: "none",
											title: res.data.msg,
											duration: 1000
										})
									}
								}
							})
					}
				}
			})
		}
	}
}
